import { request } from 'utils';

const prefix = '/api/categories';

export const getGradeConvert = () =>
  request({
    prefix,
    url: `/GradeConvert`,
    method: 'GET',
  });

export const addGradeConvert = dataAddNew => {
  const dataGradeConvert = {
    label: dataAddNew.label,
    gradeInLetter: dataAddNew.gradeInLetter,
    fromGradeIn4: parseInt(dataAddNew.fromGradeIn4),
    toGradeIn4: parseInt(dataAddNew.toGradeIn4),
    fromGrade: parseInt(dataAddNew.fromGrade),
    toGrade: parseInt(dataAddNew.toGrade),
    isCollective: true,
  };
  return request({
    prefix,
    url: `/GradeConvert`,
    data: dataGradeConvert,
    method: 'POST',
  });
};

export const getGradeConvertById = id =>
  request({
    prefix,
    url: `/GradeConvert/${id}`,
    method: 'GET',
  });

export const editGradeConvert = gradeConvert => {
  const dataGradeConvert = {
    id: gradeConvert.id,
    label: gradeConvert.label,
    gradeInLetter: gradeConvert.gradeInLetter,
    fromGradeIn4: parseInt(gradeConvert.fromGradeIn4),
    toGradeIn4: parseInt(gradeConvert.toGradeIn4),
    fromGrade: parseInt(gradeConvert.fromGrade),
    toGrade: parseInt(gradeConvert.toGrade),
    isCollective: gradeConvert.isCollective,
  };
  return request({
    prefix,
    url: `/GradeConvert/${gradeConvert.id}`,
    data: dataGradeConvert,
    method: 'PUT',
  });
};

export const deleteGradeConvert = id =>
  request({
    prefix,
    url: `/GradeConvert/${id}`,
    method: 'DELETE',
  });



