import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Checkbox } from 'antd';
import { addGradeConvert, getGradeConvertById, editGradeConvert } from 'services/GradeConvert';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    ratings: '',
    letterScores: '',
    numScores: '',
    fromScores: '',
    toScores: '',
    accumulation: false,
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormRedeemPoints extends Component {
  state = {
    teacherId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getTeacherById(match.params.id);
    }
  };

  getTeacherById = async code => {
    const teacherMap = await getGradeConvertById(code);
    const initialValue = handleInitialValue(teacherMap.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    const { teacherId } = this.state;
    this.props.form.validateFields(async (err, values) => {
      if (teacherId) {
        await editGradeConvert(values);
        this.props.history.goBack();
      } else {
        await addGradeConvert(values);
        this.props.history.goBack();
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId } = this.state;
    return (
      <Card title={teacherId ? 'Cập nhật quy đổi điểm' : 'Thêm quy đổi điểm'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item style={{ display: 'none' }} hasFeedback label="id" {...formItemLayout}>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Xếp loại" {...formItemLayout}>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Điểm chữ" {...formItemLayout}>
              {getFieldDecorator('gradeInLetter', {
                initialValue: initialValue.gradeInLetter,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input maxlength="2" />)}
            </Form.Item>
            <Form.Item hasFeedback label="Từ điểm thang 4" {...formItemLayout}>
              {getFieldDecorator('fromGradeIn4', {
                initialValue: initialValue.fromGradeIn4,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Đến điểm thang 4" {...formItemLayout}>
              {getFieldDecorator('toGradeIn4', {
                initialValue: initialValue.toGradeIn4,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Từ điểm thang 10" {...formItemLayout}>
              {getFieldDecorator('fromGrade', {
                initialValue: initialValue.fromGrade,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Đến điểm thang 10" {...formItemLayout}>
              {getFieldDecorator('toGrade', {
                initialValue: initialValue.toGrade,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tích lũy">
              {getFieldDecorator('isCollective', {
                initialValue: initialValue.isCollective,
                valuePropName: 'checked',
              })(<Checkbox />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#/redeem-points/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormRedeemPoints {...props} />
  </Page>
));



